.become-seller-step-two-main-container {
  padding-left: var(--buyer-page-margin-left);
  padding-right: var(--buyer-page-margin-right);
}

.seller-step-two-heading {
  font-size: 1.5rem;
  margin-top: 20px;
}

.table-top-container {
  width: 80%;
  margin: auto;
  margin-top: 20px;
  background-color: var(--third-background-color);
  border-radius: 5px;
}

/* td {
  border: 1px solid black;
} */

.table-container td {
  border: 1px solid black;
}
.table-container tr:first-child td {
  border-top: 0;
}
.table-container tr td:first-child {
  border-left: 0;
}
.table-container tr:last-child td {
  border-bottom: 0;
}
.table-container tr td:last-child {
  border-right: 0;
}

.table-container tr:first-child th {
  border-top: 0;
}
.table-container tr th:first-child {
  border-left: 0;
}
.table-container tr:last-child th {
  border-bottom: 0;
}
.table-container tr th:last-child {
  border-right: 0;
}
.table-container {
  border-collapse: collapse;
  width: 100%;
  margin: auto;
}
.table-container th {
  height: 50px;
  border: 1px solid black;
}
.table-container td {
  height: 60px;
}
.table-container .become-seller-first-row:first td {
  border-bottom: 0px;
  bottom: none;
}
.become-seller-first-row td{
  height: 90px;
}
.become-seller-second-row td{
  height: 90px;
}
.check{
  border-top: 0px;
  border: none;
}
.check1{
  border-bottom: 0px;
  border: none;

}
.table-container tr:first-child td:first-child {
  bottom: none;
}

