.become-seller-inner-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  width: 90%;
}
.become-seller-table-td img {
  width: 15px;
  height: 15px;
  object-fit: contain;
}

.become-seller-table-inner-btn img {
  width: 12px;
  height: 12px;
  margin-top: 3px;
  object-fit: contain;
}
.become-seller-table-inner-btn {
  display: flex;
  margin: auto;

  justify-content: space-evenly;
  align-items: center;
  width: 8.5rem;
  border-radius: 3px;
  height: 30px;
  cursor: pointer;
  background-color: var(--main-color-with-less-opacity);
  color: var(--text-color);
}

.become-seller-table-select-icon {
  width: 20px;
  height: 20px;
  border-radius: 5px;
  border: 2px solid var(--main-color);
  cursor: pointer;
  margin: auto;
}

.become-seller-table-price-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.become-seller-table-price-inner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid black;
  width: 5rem;
  padding-bottom: 3px;
  margin-right: 5px;
}
.become-seller-table-price-container img {
  width: 15px;
  height: 20px;
  object-fit: contain;
}

.become-seller-step-two-bottom-btn {
  width: 80%;
  margin: auto;
  margin-top: 30px;
}
