.become-seller-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.become-seller-left-btn {
  width: 15rem;
  font-size: 1.2rem;
  height: 50px;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #d6d6d6;
  color: black;
  cursor: pointer;
}
.become-seller-right-btn {
  width: 15rem;
  font-size: 1.2rem;
  height: 50px;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: var(--main-color);
  color: var(--text-color);
  cursor: pointer;
}

