.talent-gig-info-main-container {
  margin-left: var(--buyer-page-margin-left);
  margin-right: var(--buyer-page-margin-right);
}

.talent-gig-info-top-container {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}

.talent-gig-info-top-container-left {
  width: 25%;
}

.talent-gig-info-top-container-right {
  width: 70%;
}

.talent-gig-info-graph-container {
  width: 70%;
}

.talent-gig-info-graph-heading-containers {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  margin-bottom: 15px;
}
.talent-gig-info-graph-right-heading {
  color: var(--main-color);
}

.talent-gig-info-middle-container img {
  object-fit: contain;
  width: 100%;
}
.talent-gig-info-middle-container {
  margin-top: 10px;
  display: flex;

  justify-content: flex-end;
}

.talent-gig-info-tabcard-container {
  width: 70%;
}
.talent-gig-info-tabcard-inner-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.checkk {
  margin-left: -6vw;
  margin-right: -5.6vw;
}
