.talent-success-guide-card {
  background-color: var(--third-background-color);
  border-radius: 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}

.talent-success-guide-card-right {
  width: 30%;
  margin-right: 30px;
}
.talent-success-guide-card-left {
  width: 70%;
  margin-left: 30px;
}

.talent-success-guide-card-left-heading {
  font-size: 2rem;
  margin-left: 20px;
}

.talent-success-guide-card-left-text {
  line-height: 35px;
  display: flex;
  justify-content: flex-end;
  margin-top: 15px;
}
.talent-success-guide-card-right-img {
  display: flex;
  justify-content: flex-end;
}
.talent-success-guide-card-right img {
  width: 70%;
  object-fit: contain;
}
