.talent-manage-order-right-container {
  padding-left: 30px;
  width: 80%;
  padding-top: 20px;
  height: calc(100vh - 162px);
  overflow-y: auto;
}

.talent-manage-order-right-container::-webkit-scrollbar {
  display: none;
}
/* Hide scrollbar for IE, Edge and Firefox */
.talent-manage-order-right-container {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.talent-account-form-heading {
  font-size: 1.7rem;
  font-weight: 700;
}
.manage-orders-table {
  width: 100%;
}

