.talent-account-section-heading {
  font-size: 1.7rem;
  padding-bottom: 10px;
  border-bottom: 2px solid var(--border-color);
  padding-left: 6rem;
  font-weight: 700;
  margin-top: 5px;
}

.talent-account-section-container {
  display: flex;
  margin-right: var(--buyer-page-margin-right);
  margin-left: var(--buyer-page-margin-left);

  justify-content: flex-start;
}

.talent-account-section-sidebar-container {
  width: calc(20% - 20px);
  padding-top: 20px;
  border-right: 2px solid var(--border-color);
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  /* margin-bottom: 50px; */
  min-height: calc(100vh - 162px);
}
.talent-account-section-sidebar-inner-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  cursor: pointer;
}

.talent-account-section-sidebar-text {
  font-size: 1.7rem;
  opacity: 0.7;
}
.talent-account-section-sidebar-text-active {
  font-size: 1.7rem;
  font-weight: 700;
  color: var(--main-color);
}

.talent-account-section-sidebar-container img {
  width: 21px;
  height: 26px;
  object-fit: contain;
}

.talent-account-section-right-container {
  padding-left: 50px;
  width: 80%;
  padding-top: 20px;
}
