.review-top-container {
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  font-size: 1.2rem;
}

.review-top-heading {
  font-weight: 700;
  font-size: 2rem;
}

.review-top-stars-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.review-top-star {
  color: var(--stars-colors);
  font-size: 25px !important;
}

.review-top-text {
  color: var(--text-color-light);
  margin-top: 10px;
}

.review-progress-bar-container {
  display: flex;
  justify-content:center;
  align-items: center;
  width: 60%;
  margin-top: 30px;
}
.review-progress-bar-div {
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.review-progress-bar-circle {
  width: 100px;
  margin-top: 10px;
}

.review-progress-bar-star {
  color: var(--stars-colors);
  font-size: 15px !important;
}

.reviewer-bio-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.reviewer-bio-div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.reviewer-bio-profile {
  width: 70px;
  margin-right: 30px;
}

.reviewer-bio-profile img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.reviewer-bio-rating-stars-contianer {
  display: flex;
  justify-content: space-between;
  width: 220px;
  margin-top: 10px;
  margin-right: 30px;
}

.reviewer-progress-bar-star {
  color: var(--stars-colors);
  font-size: 40px !important;
}
.reviewer-bio-rating-contianer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.reviewer-bio-name {
  font-size: 1.5rem;
  font-weight: 700;
  margin-right: 20px;
}
.reviewer-bio-name span {
  font-size: 0.9rem;
  font-weight: normal;
}
.reviewer-rating-number {
  font-size: 1.3rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.reviewer-review-time {
  font-weight: 600;
}

.reviewer-desc {
  width: 70%;
  margin-top: 30px;
  font-size: 1.2rem;
}

.bottom-container-review {
  padding-bottom: 10px;
  border-bottom: 1px solid #000000;
  margin-top: 20px;
}
.bottom-container-review:last-child {
border: none;
}

.review-bottom-container{
    margin-top: 30px;
}