.talent-home-span {
  --talent-home-heading: 2rem;
}

.talent-home-top-section,
.talent-home-bottom-section {
  margin-left: var(--buyer-page-margin-left);
  margin-right: var(--buyer-page-margin-right);
}
.talent-home-analytics-main-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 10px;
}
.talent-home-analytics-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: var(--third-background-color);
  border-radius: 7px;
  width: 12rem;
  min-height: 5rem;
  padding: 10px;
}

.talent-home-analytics-heading {
  margin-top: 30px;
  font-size: var(--talent-home-heading);
}
.talent-home-order-heading {
  margin-top: 30px;
  font-size: var(--talent-home-heading);
}

.talent-home-analytics-price-dot {
  width: 10px;
  margin-right: 40px;
  height: 10px;
  border-radius: 50%;
  background-color: var(--light-background-color);
  margin-top: 2px;
}

.talent-home-analytics-text {
  font-size: 1.2rem;
}
.talent-home-analytics-price-container {
  width: 90%;
  margin-top: 15px;
  font-size: 1.2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.talent-home-analytics-price {
  font-size: var(--talent-home-heading);
}
.talent-goal-text {
  font-size: var(--talent-home-heading);
  margin-bottom: 20px;
}

.talent-peformance-level-text{
  font-size: var(--talent-home-heading);

}
.talent-peformance-level{
  margin-top: 4rem;
}
.talent-peformance-level-img{
  width: 100%;
}
.talent-peformance-level-img img{
  width: 100%;
  object-fit: contain;
}