.talent-about-carousel-container {
    display: flex;
    justify-content: center;
    margin-top: 30px;
  }
  .about-me-carousel-img {
    width: 100%;
    /* height: 100%; */
    object-fit: contain;
  }
  
  .talent-about-slider {
    width: 450px;
  }
  
  .talent-about-carousel-container .slick-arrow.slick-prev,
  .talent-about-carousel-container .slick-arrow.slick-next {
    background: #fcfcfc;
    width: 47px;
    height: 104px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    box-shadow: 1px 2px 10px -1px rgb(0 0 0 / 30%);
  }
  .talent-about-carousel-container .slick-prev {
    left: -20px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2rem !important;
    width: 2rem !important;
    background-color: white;
    border-radius: 50%;
    cursor: pointer;
    /* transform: rotate(180deg) !important; */
    box-shadow: 5px 5px 5px #ccc;
  }
  .talent-about-carousel-container .slick-next {
    right: -20px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2rem !important;
    width: 2rem !important;
    background-color: white;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 5px 5px 5px #ccc;
  }
  .slick-prev::before,
  .slick-next::before {
    display: none;
  }
  


