.buyer-profile-list-container {
  margin-right: var(--buyer-page-margin-right);
  margin-left: var(--buyer-page-margin-left);
  margin-top: 30px;
  --small-font-size: 10px;
  --heading-font-size: 12px;
}

.buyer-profile-list-top-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.buyer-profile-list-top-left-container {
  display: flex;
  justify-content: space-between;
}

.buyer-profile-list-search-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--third-background-color);
  height: 60px;
  margin-left: 20px;
  border-radius: 50px;
  border: 1px solid var(--main-color);
  width: 400px;
}

.buyer-profile-list-search-container img {
  width: 30px;
  margin-left: 30px;
  height: 30px;
}

.buyer-profile-list-search-container input[type="text"]::placeholder {
  font-size: 1.2rem;
  opacity: 0.5;
}
.buyer-profile-list-search-container input[type="text"] {
  width: calc(100% - 90px);
  height: 100%;
  padding-left: 30px;
  border: none;
  outline: none;
  background-color: var(--third-background-color);
  border-radius: 50px;
  /* background-color: red; */
}

.buyer-profile-list-top-right-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.buyer-profile-all-btn-container img {
  width: 15px;
  height: 10px;
}
.buyer-profile-all-btn-container {
  display: flex;
  width: 5rem;
  background-color: var(--third-background-color);
  border: 1px solid var(--main-color);
  border-radius: 10px;
  justify-content: space-around;
  height: 40px;
  align-items: center;
  cursor: pointer;
  margin-right: 20px;
}

.buyer-profile-all-categories-btn-icon {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.buyer-profile-list-bottom-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-top: 50px;
}

.buyer-profile-list-item-container {
  width: 18%;
  margin-bottom: 50px;
  margin-left: 10px;
  margin-right: 10px;
  position: relative;
  /* height: 15rem; */
  display: flex;
  justify-content: center;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 10px;
  font-size: var(--small-font-size);
  z-index: -1;
}
.buyer-profile-list-item-inner-container {
  width: 90%;
  margin-top: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.buyer-profile-list-item-top-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.buyer-profile-list-item-top-img-container img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.buyer-profile-list-item-top-img-container {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 20px;
  border: 3px solid var(--main-color);
}

.buyer-profile-list-item-top-info-rating {
  display: flex;
  justify-content: center;
}
.buyer-profile-list-item-top-info-rating-middle {
  margin-left: 10px;
  margin-right: 10px;
}
.buyer-profile-list-item-top-info-rating-left {
  display: flex;
}

.buyer-profile-list-item-project-info-heading-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
}
.buyer-profile-list-item-project-info-heading {
  font-size: var(--heading-font-size);
  color: var(--text-color-light);
}

.buyer-profile-list-item-project-info-desc {
  display: flex;
  justify-content: flex-start;
  width: 90%;
  margin-left: 10px;
  font-size: var(--small-font-size);
  margin-top: 5px;
}
.buyer-profile-list-item-project-budget-container {
  border-top: 1px solid var(--border-color);
  margin-top: 5px;
  padding-top: 5px;
  color: var(--text-color-light);

  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 30px;
}

.buyer-profile-list-item-project-budget-div {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.buyer-profile-list-item-btn-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--main-color);
  color: var(--text-color);
  border-radius: 50px;
  height: 34px;
  width: 9rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  text-align: center;
  position: absolute;
  bottom: -17px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  cursor: pointer;
  font-size: var(--heading-font-size);
}
.buyer-profile-list-item-btn-text {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 25px;
}
.buyer-profile-list-item-btn-container img {
  width: 20px;
  margin-top: 5px;
  height: 10px;
}

.buyer-profile-list-item-top-info-container h2 {
  font-weight: 500;
  color: #000000;
  opacity: 0.6;
}
