.account-security-container {
  padding-right: 9vw;
  padding-left: 9vw;
  padding-top: 20px;
}

.account-security-top-container {
  display: flex;
  justify-content: space-between;
  width: 70%;
  align-items: center;
}
.account-security-back-btn-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.account-security-back-btn-container img {
  margin-right: 10px;
  width: 30px;
  height: 20px;
  transform: rotate(-90deg);
}
.account-security-back-btn-text {
  font-size: 1.5rem;
  font-weight: 500;
}

.account-security-right-text {
  font-size: 2rem;
  font-weight: 500;
}

.account-security-bottom-container {
  width: 100%;
  height: 500px;
  background-color: var(--secondary-background-color);
  margin-top: 10px;
  border-radius: 30px;
  display: flex;
  justify-content: space-between;
  /* padding: 10px; */
  padding: 30px;
}

.account-security-subject-input::placeholder {
  font-size: 1.2rem;
  font-style: italic;
}
.ccount-security-detail-input::placeholder {
  font-size: 1.2rem;
  font-style: italic;
}
.account-security-bottom-left-container label {
  font-weight: 500;
  font-size: 1.2rem;
}
.account-security-subject-input {
  border: 2px solid var(--light-background-color);
  border-radius: 20px;
  outline: none;
  padding: 20px;
  padding-left: 40px;
  width: calc(100% - 20px);
  background-color: var(--secondary-background-color);
}
.account-security-detail-input {
  border: 2px solid var(--light-background-color);
  border-radius: 20px;
  outline: none;
  padding: 20px;
  padding-left: 40px;
  width: calc(100% - 20px);
  background-color: var(--secondary-background-color);
  resize: none;
}
.account-security-bottom-left-container {
  width: 50%;
}
.account-security-bottom-right-container {
  width: 50%;
  display: flex;
  justify-content: center;
}

.account-security-img {
  margin-top: 10px;
  width: 300px;
  height: 300px;
}
.account-security-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.account-security-btn {
  display: flex;
  justify-content: center;
  width: 60%;
  margin: auto;
  /* left: 0;
  right: 0; */
  align-items: center;
  margin-top: 20px;
}
